<template>
  <ContactList type="send-card" @onContactClick="contactSelectHandler" @newContact="onNewContact" @back="drawerClose" />
</template>
<script>
import ContactList from '@m/contact/views/ContactList';
import { mapMutations } from 'vuex';
export default {
  components: {
    ContactList
  },
  methods: {
    ...mapMutations('contact', ['setSelectedContact']),
    contactSelectHandler(contact) {
      this.setSelectedContact(contact);
      this.drawerPush('sendCard-form');
    },
    onNewContact() {
      this.drawerPush('sendCard-newContact');
    }
  }
};
</script>
